import en from './en';
import zh from './zh';
import Vue from 'vue'
import locale from 'element-ui/lib/locale';
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)
//从localStorage获取语言选择。
const i18n = new VueI18n({
    locale: localStorage.getItem('languageSet')||'zh', 
    messages:{
      en:en,
      zh:zh
  }
})
locale.i18n((key, value) => i18n.t(key, value)) //兼容element

export default i18n